<template>
  <div class="operate-dialog">
    <el-dialog
      class="createTask-dialog"
      title="提示"
      :visible.sync="visible"
      :fullscreen="true"
      :show-close="false"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <div slot="title" class="dialog-header">
        <el-button class="back" @click="createTaskDialogClose">返回</el-button>
        <span class="header-text">{{ createTaskDialogTitle }}</span>
      </div>
      <div class="dialog-content">
        <el-form
          class="createTask-form"
          :model="createTaskForm"
          ref="createTaskForm"
          :rules="createTaskFormRules"
          label-width="160px"
        >
          <div class="base-info">
            <div class="title">
              <span class="text">关联关系基本信息</span>
            </div>
            <div class="content">
              <el-row>
                <el-col :span="8">
                  <el-form-item label="关联关系名称：" prop="relationName">
                    <el-input
                      size="mini"
                      class="input"
                      :disabled="isCheck"
                      v-model="createTaskForm.relationName"
                    ></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label="关联关系编号：">
                    <el-input
                      size="mini"
                      class="input"
                      :disabled="true"
                      v-model="createTaskForm.relationNumber"
                    ></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="24">
                  <el-form-item label="关联关系描述：" prop="descibe">
                    <el-input
                      class="input-row"
                      :disabled="isCheck"
                      type="textarea"
                      :rows="5"
                      :resize="'none'"
                      placeholder="请输入内容"
                      v-model="createTaskForm.descibe"
                    ></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="6">
                  <el-form-item :label="isCheck ? '主体关联关系描述：' : '关联关系选择：'" prop="targetId">
                    <el-select
                      size="mini"
                      class="input"
                      :disabled="isCheck"
                      style="width: 100%"
                      v-model="createTaskForm.targetId"
                      placeholder="请选择"
                      @change="getTagTargetNameOptions"
                    >
                      <el-option
                        v-for="item in targetIdOptions"
                        :key="item.value"
                        :label="item.name"
                        :value="item.value"
                        :disabled="item.disabled"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col class="line" :span="1">
                  <span>-</span>
                </el-col>
                <el-col :span="4">
                  <el-form-item :label-width="'0px'" prop="recId">
                    <el-select
                      size="mini"
                      class="input"
                      :disabled="isCheck"
                      style="width: 100%"
                      v-model="createTaskForm.recId"
                      placeholder="请选择"
                      @change="getTagRecNameOptions"
                    >
                      <el-option
                        v-for="item in recIdOptions"
                        :key="item.value"
                        :label="item.name"
                        :value="item.value"
                        :disabled="item.disabled"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
              </el-row>
            </div>
          </div>
          <div class="label-setup">
            <div class="title">
              <span class="text">
                {{
                isCheck ? "关联主体标签描述" : "关联主体标签设置"
                }}
              </span>
            </div>
            <div class="content">
              <el-table
                :data="createTaskForm.listRelationPair"
                border
                :height="287"
                :header-cell-style="headStyle"
                style="width: 100%; height: 100%"
              >
                <el-table-column prop="tagTargetId" label="目标主体的标签">
                  <template slot-scope="scope">
                    <el-select
                      size="mini"
                      style="width: 80%; padding: 10px"
                      v-model="scope.row.tagTargetId"
                      placeholder="请选择"
                      :disabled="tagTargetNameOptions.length === 0 || isCheck"
                    >
                      <el-option
                        v-for="item in tagTargetNameOptions"
                        :key="item.value"
                        :label="item.name"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </template>
                </el-table-column>
                <el-table-column prop="tagRecId" label="推荐主体的标签">
                  <template slot-scope="scope">
                    <el-select
                      size="mini"
                      style="width: 80%; padding: 10px"
                      v-model="scope.row.tagRecId"
                      placeholder="请选择"
                      :disabled="tagRecNameOptions.length === 0 || isCheck"
                    >
                      <el-option
                        v-for="item in tagRecNameOptions"
                        :key="item.value"
                        :label="item.name"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                    <!-- <span v-else>{{ scope.row.tagRecName }}</span> -->
                  </template>
                </el-table-column>
                <el-table-column prop="weight" label="权重" width="200">
                  <template slot-scope="scope">
                    <el-input size="mini" class="input" v-if="!isCheck" v-model="scope.row.weight"></el-input>
                    <span v-else>{{ scope.row.weight }}</span>
                  </template>
                </el-table-column>
                <el-table-column v-if="!isCheck" label="操作" width="100">
                  <template slot-scope="scope">
                    <el-button
                      type="text"
                      :disabled="scope.$index === 0"
                      @click="delListRelationPair(scope.$index, scope.row)"
                    >删除</el-button>
                  </template>
                </el-table-column>
              </el-table>
              <div class="add-label" v-if="!isCheck">
                <span @click="addLabelSetup" style="cursor: pointer">
                  <em class="el-icon-plus icon"></em>
                  <span class="text">添加关联关系对</span>
                </span>
              </div>
            </div>
          </div>
          <div class="rule-setup" v-if="!isCheck">
            <div class="title">
              <span class="text">过滤规则设置</span>
            </div>
            <div class="content">
              <el-row>
                <el-col :span="8">
                  <el-form-item label="过滤对象：">
                    <el-select
                      size="mini"
                      style="width: 80%"
                      v-model="createTaskForm.blacklistObject"
                      placeholder="请选择"
                    >
                      <el-option
                        v-for="item in blacklistObjectOptions"
                        :key="item.value"
                        :label="item.name"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="16">
                  <el-form-item label="黑名单过滤：">
                    <div class="button-group">
                      <el-button class="button" size="mini" @click="downloadUpload">下载上传模板</el-button>
                      <el-upload
                        :style="{display: 'inline-block'}"
                        action="/portal/blackList/uploadBlackList"
                        :data="{menuType:'0',objType:createTaskForm.blacklistObject,objId:createTaskForm.blacklistObject==='1'?createTaskForm.targetId:createTaskForm.recId,}"
                        :on-change="uploadObject"
                        :show-file-list="false"
                        :disabled="!createTaskForm.blacklistObject"
                        accept=".xlsx, .xls"
                      >
                        <el-button
                          size="mini"
                          class="button"
                          :disabled="!createTaskForm.blacklistObject"
                        >上传对象清单</el-button>
                      </el-upload>
                      <el-button
                        size="mini"
                        :disabled="!createTaskForm.blacklistObject"
                        @click="chooseHistory"
                      >从过滤历史中选择</el-button>
                      <el-button size="mini" @click="resultPreview">推荐结果预览</el-button>
                    </div>
                  </el-form-item>
                </el-col>
              </el-row>
            </div>
          </div>
          <div class="result-setup">
            <div class="title">
              <span class="text">{{ isCheck ? "推荐结果" : "推荐结果设置" }}</span>
            </div>
            <div class="content">
              <el-row>
                <el-col :span="8">
                  <el-form-item label="最大推荐结果数：" prop="recResultNumber">
                    <el-input
                      size="mini"
                      class="input"
                      :disabled="isCheck"
                      v-model="createTaskForm.recResultNumber"
                    ></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label="推荐结果为空时：">
                    <el-radio-group v-model="createTaskForm.recEmpty" :disabled="isCheck">
                      <el-radio :label="'0'">不处理</el-radio>
                      <el-radio :label="'1'">随机分配</el-radio>
                    </el-radio-group>
                  </el-form-item>
                </el-col>
                <el-col :span="8" v-if="!isCheck">
                  <el-form-item :label-width="'0px'">
                    <span>（当目标对象无具体推荐对象时，给目标对象随机分配主体内的对象）</span>
                  </el-form-item>
                </el-col>
              </el-row>
            </div>
          </div>
          <div class="update-setup" v-if="!isCheck">
            <div class="title">
              <span class="text">更新配置</span>
            </div>
            <div class="content">
              <el-row>
                <el-col :span="24">
                  <el-form-item label="更新周期：">
                    <el-radio-group v-model="createTaskForm.updateCycle">
                      <!-- <el-radio :label="'0'">指定时间</el-radio> -->
                      <el-radio :label="'1'">日更新</el-radio>
                      <el-radio :label="'2'">周更新</el-radio>
                      <el-radio :label="'3'">月更新</el-radio>
                      <el-radio :label="'4'">年更新</el-radio>
                    </el-radio-group>
                  </el-form-item>
                </el-col>
              </el-row>
              <!-- <el-form-item label="更新时间：">
                <el-row>
                  <el-col :span="4">
                    <el-select
                      size="mini"
                      style="width: 80%"
                      v-model="updateWeek"
                      placeholder="请选择"
                      v-if="createTaskForm.updateCycle==='2'"
                    >
                      <el-option
                        v-for="item in weekOptions"
                        :key="item.value"
                        :label="item.name"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </el-col>
                  <el-col :span="8">
                    <el-date-picker
                      size="mini"
                      class="input"
                      v-model="updateDetail"
                      type="datetime"
                      range-separator="至"
                      value-format="yyyy-MM-dd HH:mm:ss"
                      start-placeholder="开始日期"
                      end-placeholder="结束日期"
                    ></el-date-picker>
                  </el-col>
                </el-row>
              </el-form-item>-->

              <el-row>
                <el-col :span="8">
                  <el-form-item label="更新有效期：">
                    <el-date-picker
                      size="mini"
                      class="input"
                      v-model="createTaskForm.updateTime"
                      type="datetimerange"
                      range-separator="至"
                      value-format="yyyy-MM-dd HH:mm:ss"
                      start-placeholder="开始日期"
                      end-placeholder="结束日期"
                    ></el-date-picker>
                  </el-form-item>
                </el-col>
                <el-col :span="5">
                  <el-form-item label="更新模式：">
                    <el-select
                      size="mini"
                      style="width: 80%"
                      v-model="createTaskForm.updateModel"
                      placeholder="请选择"
                    >
                      <el-option
                        v-for="item in updateModelOptions"
                        :key="item.value"
                        :label="item.name"
                        :value="item.value"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item :label-width="'0px'">
                    <span>（配置完成后立即执行更新，后续按照更新配置）</span>
                  </el-form-item>
                </el-col>
              </el-row>
            </div>
          </div>
          <div class="update-setup" v-if="type === 'approval'">
            <div class="title">
              <span class="text">执行信息</span>
            </div>
            <div class="content">
              <el-row>
                <el-col :span="8">
                  <el-form-item label="执行方式：" prop="exeType">
                    <span>{{ createTaskForm.exeType }}</span>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label="执行渠道：" prop="exeChannel">
                    <span>{{ createTaskForm.exeChannel }}</span>
                  </el-form-item>
                </el-col>
                <el-col :span="8">
                  <el-form-item label="申请人：" prop="applicant">
                    <span>{{ createTaskForm.applicant }}</span>
                  </el-form-item>
                </el-col>
              </el-row>
            </div>
          </div>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" v-if="!isCheck" @click="createTaskSave">保存</el-button>
        <el-button @click="createTaskDialogClose">
          {{
          isCheck ? "关闭" : "取消"
          }}
        </el-button>
      </span>
    </el-dialog>
    <el-dialog
      class="filterHistory-dialog"
      title="提示"
      v-if="filterHistoryDialogVisible"
      :visible.sync="filterHistoryDialogVisible"
      width="70%"
      :before-close="filterHistoryClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ "过滤历史" }}</span>
      </span>
      <!-- <span class="table-title">过滤历史列表</span> -->
      <div class="dialog-search">
        <el-form class="dialog-form" :model="filterHistoryForm" label-width="160px">
          <el-row>
            <el-col :span="12">
              <el-form-item label="显示字段：" prop="historyField">
                <el-select
                  size="mini"
                  class="input"
                  multiple
                  v-model="filterHistoryForm.historyField"
                  placeholder="请选择"
                  collapse-tags
                >
                  <el-option
                    v-for="item in historyFieldOptions"
                    :key="item.value"
                    :label="item.name"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label label-width="0px">
                <el-button type="primary" size="mini" @click="getFilterHistoryData('confirm')">展示</el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <bdaTable
        class="dialog-table"
        :tableData="filterHistoryData"
        :headerColor="'#1899fa'"
        :selectWidth="60"
        @select="filterHistorySelectData"
      ></bdaTable>
      <!-- <span v-if="this.type==='edit'" class="table-title">已存过滤历史列表</span>
      <bdaTable
        v-if="this.type==='edit'"
        class="dialog-table"
        :tableData="filterHistorySaveData"
        :headerColor="'#1899fa'"
        :selectWidth="60"
        @select="filterHistorySelectSaveData"
      ></bdaTable>
      -->
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="filterHistorySelectConfirm">确定</el-button>
      </span>
    </el-dialog>
    <resultPreviewDialog
      v-if="resultPreviewDialogVisible"
      :visible.sync="resultPreviewDialogVisible"
      :showData="createTaskForm"
      :type="resultPreviewDialogType"
    ></resultPreviewDialog>
  </div>
</template>

<script>
import Interface from "@/base/mixin/recommend-management";
import resultPreviewDialog from "./result-preview-dialog";
import bdaTable from "@/components/bda-table/bda-table";
import { exportExcelGet } from "@/utils/export";

export default {
  components: {
    bdaTable,
    resultPreviewDialog
  },
  mixins: [Interface],

  props: {
    visible: {
      type: Boolean,
      default: () => {
        return false;
      }
    },
    showData: {
      type: Object,
      default: () => {
        return {};
      }
    },
    type: {
      type: String,
      default: () => {
        return "add";
      }
    }
  },
  //   mixins: [Interface],
  data() {
    return {
      //是否为查看或详情
      isCheck: false,
      //标题名称
      createTaskDialogTitle: "",
      //推荐任务-表单验证规则
      createTaskFormRules: {
        relationName: [
          { required: true, message: "请输入关联关系名称", trigger: "blur" }
        ],
        descibe: [
          { required: true, message: "请输入关联关系描述", trigger: "blur" }
        ],
        targetId: [
          { required: true, message: "请选择目标主体", trigger: "blur" }
        ],
        recId: [{ required: true, message: "请选择推荐主体", trigger: "blur" }],
        recResultNumber: [
          { required: true, message: "请输入最大推荐结果数", trigger: "blur" }
        ]
      },
      //推荐任务-表单数据
      createTaskForm: {
        menuType: "",
        relationName: "",
        relationNumber: "",
        descibe: "",
        targetId: "",
        recId: "",
        listRelationPair: [
          {
            tagId: "",
            tagTargetId: "",
            tagRecId: "",
            tagTargetName: "",
            tagRecName: "",
            weight: "1"
          }
        ],
        blacklist: [],
        blacklistObject: "",
        recResultNumber: "",
        recEmpty: "1",
        updateCycle: "",
        updateTime: [],
        updateModel: "",
        updatePeriod: "",
        exeType: "",
        exeChannel: "",
        applicant: ""
      },
      //更新周数
      updateWeek: "",
      //更新时间
      updateDetail: "",
      //推荐任务-选择框数据
      //目标主体
      targetIdOptions: [],
      //推荐主体
      recIdOptions: [],
      //目标主体的标签
      tagTargetNameOptions: [],
      //推荐主体的标签
      tagRecNameOptions: [],
      //过滤对象
      blacklistObjectOptions: [],
      //更新模式
      updateModelOptions: [],
      //周几
      weekOptions: [
        { name: "周一", value: "1" },
        { name: "周二", value: "2" },
        { name: "周三", value: "3" },
        { name: "周四", value: "4" },
        { name: "周五", value: "5" },
        { name: "周六", value: "6" },
        { name: "周日", value: "7" }
      ],
      //表头颜色
      headStyle: { background: "#1895f9" },
      //过滤历史
      filterHistoryDialogVisible: false,
      //列表数据
      filterHistoryForm: {
        keyValue: "",
        keyName: "",
        historyField: []
      },
      filterHistoryData: {},
      filterHistoryMultipleData: [],
      filterHistoryColumnData: [],
      //目标主体的字段
      historyFieldOptions: [],
      //已存数据列表
      filterHistorySaveData: {},
      filterHistorySaveMultipleData: [],
      //推荐结果预览
      resultPreviewDialogVisible: false,
      resultPreviewDialogData: {},
      resultPreviewDialogType: ""
    };
  },
  watch: {},
  created() {
    this.createTaskForm.menuType = window.location.href.split("/")[
      window.location.href.split("/").length - 1
    ];
    console.log(this.createTaskForm.menuType, " this.createTaskForm.menuType ");
  },
  mounted() {
    this.typeDeal();
    this.getTargetIdOptions();
    this.getBlacklistObjectOptions();
    this.getUpdateModelOptions();
  },
  methods: {
    //类型处理
    typeDeal() {
      if (
        this.type === "detail" ||
        this.type === "check" ||
        this.type === "approval"
      ) {
        this.isCheck = true;
        this.createTaskDialogTitle = "关联关系详细信息";
        this.dataInit();
      }
      if (this.type === "edit") {
        this.dataInit();
        this.createTaskDialogTitle = "编辑推荐生成任务";
        this.isCheck = false;
      }
      if (this.type === "add") {
        this.isCheck = false;
        this.createTaskDialogTitle = "新增推荐生成任务";
      }
    },
    //数据处理
    dataInit() {
      let data = {
        ...this.showData
      };

      this.createTaskForm.relationName = data.relationName || "";
      this.createTaskForm.relationNumber = data.relationNumber || "";
      this.createTaskForm.descibe = data.descibe || "";
      this.createTaskForm.targetId = data.targetId || "";
      this.createTaskForm.recId = data.recId || "";
      this.getTagTargetNameOptions(data.targetId);
      this.getTagRecNameOptions(data.recId);
      this.createTaskForm.listRelationPair = data.listRelationPair || [
        {
          tagId: "",
          tagTargetId: "",
          tagRecId: "",
          tagTargetName: "",
          tagRecName: "",
          weight: "1"
        }
      ];
      this.createTaskForm.recResultNumber = data.recResultNumber || "";
      this.createTaskForm.recEmpty = data.recEmpty || "";
      this.createTaskForm.updateCycle = data.updateCycle || "";
      this.createTaskForm.updateModel = data.updateModel || "";
      this.createTaskForm.updateTime = [
        data.validityTimeStart || "",
        data.validityTimeEnd || ""
      ];
      // this.createTaskForm.updatePeriod = this.dealWeek('back', data.updatePeriod)
      this.createTaskForm.exeType = data.exeType || "";
      this.createTaskForm.exeChannel = data.exeChannel || "";
      // this.createTaskForm.blacklist = data.blacklist || '';
      this.createTaskForm.blacklistObject = data.blacklistObject || "";
      this.createTaskForm.applicant = data.applicant || "";
    },
    // 新增推荐生成任务保存
    createTaskSave() {
      console.log(this.createTaskForm, "this.createTaskForm");
      let isRepeat = false;
      this.createTaskForm.listRelationPair.forEach(e => {
        if (!e.tagTargetId || !e.tagRecId) {
          isRepeat = true;
        }
      });
      if (isRepeat) {
        this.$message.warning("主体标签不能为空");
        return;
      }
      this.createTaskForm.listRelationPair.forEach(e1 => {
        let count = 0;
        this.createTaskForm.listRelationPair.forEach(e2 => {
          if (
            e1.tagTargetId === e2.tagTargetId &&
            e1.tagRecId === e2.tagRecId
          ) {
            count++;
          }
        });
        if (count > 1) {
          isRepeat = true;
        }
      });

      if (isRepeat) {
        this.$message.warning("关联关系对不能重复");
        return;
      }
      // this.createTaskForm.updatePeriod = this.dealWeek('save')
      this.$refs["createTaskForm"].validate(valid => {
        if (valid) {
          let params = {
            menuType: this.createTaskForm.menuType,
            relationNumber: this.createTaskForm.relationNumber,
            relationName: this.createTaskForm.relationName,
            descibe: this.createTaskForm.descibe,
            listRelationPair: this.createTaskForm.listRelationPair,
            blacklist: this.createTaskForm.blacklist,
            recResultNumber: this.createTaskForm.recResultNumber,
            recEmpty: this.createTaskForm.recEmpty,
            updateCycle: this.createTaskForm.updateCycle,
            updateModel: this.createTaskForm.updateModel,
            updatePeriod: this.createTaskForm.updatePeriod,
            validityTimeStart: this.createTaskForm.updateTime[0],
            validityTimeEnd: this.createTaskForm.updateTime[1],
            targetId: this.createTaskForm.targetId,
            recId: this.createTaskForm.recId
          };
          if (this.type === "add") {
            this.insertRecommendGenerateTask(params)
              .then(res => {
                console.log(res, "insertRecommendGenerateTask");
                this.$message.success("新增成功");
                this.$emit("operateStauts", "success");
                this.$emit("update:visible", false);
              })
              .catch(err => {
                if (err) {
                  this.$emit("operateStauts", "fail");
                  this.$emit("update:visible", false);
                }
              });
          }
          if (this.type === "edit") {
            console.log(params, "params");
            this.updateRecommendGenerateTask(params)
              .then(res => {
                console.log(res, "updateRecommendGenerateTask");
                this.$message.success("编辑成功");
                this.$emit("operateStauts", "success");
                this.$emit("update:visible", false);
              })
              .catch(err => {
                if (err) {
                  this.$emit("operateStauts", "fail");
                  this.$emit("update:visible", false);
                }
              });
          }
        } else {
          return false;
        }
      });
    },
    // 新增推荐生成任务关闭
    createTaskDialogClose() {
      // this.createTaskDialogVisible = false
      this.$emit("update:visible", false);
    },
    //删除关联主体标签
    delListRelationPair(index, data) {
      console.log(index, data, "delListRelationPair");
      this.createTaskForm.listRelationPair.splice(index, 1);
    },
    //新增关联关系对
    addLabelSetup() {
      this.createTaskForm.listRelationPair.push({
        tagId: "",
        tagTargetId: "",
        tagRecId: "",
        tagTargetName: "",
        tagRecName: "",
        weight: "1"
      });
      console.log(
        this.createTaskForm.listRelationPair,
        " this.createTaskForm.listRelationPair"
      );
    },
    //下载上传模板
    downloadUpload() {
      exportExcelGet("/blackList/downloadBlackListDemo");
    },
    //上传对象清单
    uploadObject(file) {
      console.log(file, "file");

      if (file.status === "success") {
        this.$message.success("上传成功");
      }
      if (file.status === "fail") {
        this.$message.error("上传失败");
      }
    },

    //从过滤历史中选择
    chooseHistory() {
      this.getFilterHistoryData();
      // if (this.type === 'edit') this.getFilterHistorySaveData();
      this.filterHistoryDialogVisible = true;
    },
    //过滤历史列表选择
    filterHistorySelectData(data) {
      this.filterHistoryMultipleData = data;
    },
    //过滤历史列表选择
    filterHistorySelectSaveData(data) {
      this.filterHistorySaveMultipleData = data;
    },
    //过滤历史选择确认
    filterHistorySelectConfirm() {
      let blacklist = [];
      this.filterHistoryMultipleData.forEach(e => {
        blacklist.push(e.BLACKNUMBER);
      });

      this.createTaskForm.blacklist = blacklist;
      console.log(this.createTaskForm.blacklist, "filterHistorySelectConfirm");

      // this.filterHistoryMultipleData = [];
      this.filterHistoryDialogVisible = false;
    },
    //关闭过滤历史中
    filterHistoryClose() {
      this.filterHistoryDialogVisible = false;
    },
    //推荐结果预览
    resultPreview() {
      if (!this.createTaskForm.targetId || !this.createTaskForm.recId) {
        this.$message.warning("请选择关联关系");
        return;
      }
      if (
        !this.createTaskForm.listRelationPair[0].tagTargetId ||
        !this.createTaskForm.listRelationPair[0].tagRecId
      ) {
        this.$message.warning("请设置关联主体标签");
        return;
      }
      this.resultPreviewDialogType = "preview";
      this.resultPreviewDialogVisible = true;
    },
    //获取目标主体选项
    getTargetIdOptions() {
      this.selectTargetAndSecSubject({}).then(res => {
        this.targetIdOptions = [];
        this.recIdOptions = [];
        res.forEach(e => {
          this.targetIdOptions.push({
            name: e.tagobjName,
            value: e.tagobjId
          });
          this.recIdOptions.push({
            name: e.tagobjName,
            value: e.tagobjId
          });
        });
      });
    },
    //获取目标主体标签选项
    getTagTargetNameOptions(val) {
      this.createTaskForm.listRelationPair.forEach(e => {
        e.tagTargetId = "";
      });
      if (val) {
        //禁用选项
        let options = this.recIdOptions;
        options.forEach(e => {
          if (e.value === val) {
            e.disabled = true;
          } else {
            e.disabled = false;
          }
        });
        this.recIdOptions = options;
        //查询标签
        // this.selectTargetSubjectDesc({ targetObjId: val }).then(res => {
        //   this.tagTargetNameOptions = []
        //   res.forEach(e => {
        //     this.tagTargetNameOptions.push({
        //       name: e.tagName, value: e.tagId
        //     })
        //   })
        // })
      }
    },
    //获取推荐主体标签选项
    getTagRecNameOptions(val) {
      this.createTaskForm.listRelationPair.forEach(e => {
        e.tagRecId = "";
      });
      //禁用选项
      let options = this.targetIdOptions;
      this.targetIdOptions.forEach(e => {
        if (e.value === val) {
          e.disabled = true;
        } else {
          e.disabled = false;
        }
      });
      this.targetIdOptions = options;
      //查询标签
      if (val) {
        this.selectSecSubjectDesc({ recObjId: val }).then(res => {
          this.tagRecNameOptions = [];
          res.forEach(e => {
            this.tagRecNameOptions.push({
              name: e.tagName,
              value: e.tagId
            });
          });
        });
      }
    },
    //获取黑名单过滤对象
    getBlacklistObjectOptions() {
      this.blacklistObjectOptions = [
        { name: "目标对象", value: "1" },
        { name: "推荐对象", value: "0" }
      ];
    },
    //获取过滤历史列表
    getFilterHistoryData(val) {
      //     if (this.createTaskForm.blacklistObject === '001') {
      // }
      //   if (this.createTaskForm.blacklistObject === '002') {
      // }
      console.log(val, "val");
      if (val) {
        this.filterHistoryForSelect({
          relationNumber: this.createTaskForm.relationNumber,
          menuType: this.createTaskForm.menuType,
          objType: this.createTaskForm.blacklistObject
        }).then(res => {
          let filterHistoryColumnData = [];
          this.filterHistoryForm.historyField.forEach(e => {
            filterHistoryColumnData.push({
              prop: e,
              label: this.filterField(e)
            });
          });
          this.filterHistoryColumnData = filterHistoryColumnData;
          console.log(filterHistoryColumnData, "filterHistoryColumnData ");
          this.filterHistoryData = {
            tableData: [...res.result],
            columnNameData: this.filterHistoryColumnData
          };
        });
      } else {
        this.filterHistoryForSelect({
          relationNumber: this.createTaskForm.relationNumber,
          menuType: this.createTaskForm.menuType,
          objType: this.createTaskForm.blacklistObject
        }).then(res => {
          this.getHistoryFieldOptions(res.showField[1]);
          console.log(this.filterHistoryColumnData, "dbcolumnName");
          this.filterHistoryData = {
            tableData: [...res.result],
            columnNameData: this.filterHistoryColumnData
          };
        });
      }
    },
    //获取字段
    getHistoryFieldOptions(data) {
      this.filterHistoryColumnData = [];
      this.filterHistoryForm.historyField = [];
      this.historyFieldOptions = data.map(e => {
        if (e.isKey) {
          this.filterHistoryForm.keyValue = e.dbcolumnName;
          this.filterHistoryForm.keyName = e.dbcolumnAS;
        }
        return {
          name: e.dbcolumnTitle,
          value: e.dbcolumnAS
        };
      });

      this.filterHistoryForm.historyField.push(this.filterHistoryForm.keyName);
      this.filterHistoryColumnData.push({
        prop: this.filterHistoryForm.keyName,
        label: this.filterField(this.filterHistoryForm.keyName)
      });
    },
    //获取过滤历史列表
    getFilterHistorySaveData() {
      this.$nextTick(() => {
        this.filterHistorySaveData = {
          tableData: [
            {
              object: "用电客户",
              objectNumber: "0306003723",
              objectName: "张三",
              filterMatter: "无",
              filterTime: "2020.10.30 23:00:00",
              filterPerson: "张三"
            },
            {
              object: "用电客户",
              objectNumber: "0306003723",
              objectName: "张三",
              filterMatter: "无",
              filterTime: "2020.10.30 23:00:00",
              filterPerson: "张三"
            },
            {
              object: "用电客户",
              objectNumber: "0306003723",
              objectName: "张三",
              filterMatter: "无",
              filterTime: "2020.10.30 23:00:00",
              filterPerson: "张三"
            },
            {
              object: "用电客户",
              objectNumber: "0306003723",
              objectName: "张三",
              filterMatter: "无",
              filterTime: "2020.10.30 23:00:00",
              filterPerson: "张三"
            },
            {
              object: "用电客户",
              objectNumber: "0306003723",
              objectName: "张三",
              filterMatter: "无",
              filterTime: "2020.10.30 23:00:00",
              filterPerson: "张三"
            }
          ],
          columnNameData: [
            { prop: "object", label: "对象" },
            { prop: "objectNumber", label: "对象编号" },
            { prop: "objectName", label: "对象名称" },
            { prop: "filterMatter", label: "过滤事件" },
            { prop: "filterTime", label: "过滤时间" },
            { prop: "filterPerson", label: "过滤操作人" }
          ]
        };
      });
    },
    //获取目标主体标签选项
    getUpdateModelOptions() {
      this.updateModelOptions = [
        { name: "全量更新", value: "0" },
        { name: "增量更新", value: "1" }
      ];
    },
    //返回字段名
    filterField(value) {
      let options = [...this.historyFieldOptions];
      let name = "";
      options.forEach(e => {
        if (e.value === value) {
          name = e.name;
        }
      });
      return name;
    },
    //处理周日期
    dealWeek(type, data) {
      let value = "";
      if (type === "save" && this.createTaskForm.updateCycle === "2") {
        value = this.updateDetail + "^^" + this.updateWeek;
      } else if (type === "back") {
        value = data;
        this.updateDetail = data.split("^^")[0] || "";
        this.updateWeek = data.split("^^")[1] || "";
      } else {
        value = this.updateDetail;
      }

      return value;
    }
  }
};
</script>

